





































































import Vue from 'vue';
import UpdateMemberGroupNameModal from '@/components/member_group_maker/UpdateMemberGroupNameModal.vue';
import MemberGroupApi from '@/apis/MemberGroupApi';
import { MemberGroup, MemberModel, UpdateMemberPayload } from '@/apis/models/MemberGroupModel';

export default Vue.extend({
  components: { UpdateMemberGroupNameModal },
  data() {
    return {
      members: [] as MemberModel[],
    };
  },
  computed: {
    workspaceId(): string {
      return this.$store.state.currentWorkspace.uuid;
    },
    memberGroupId(): string {
      return this.$store.state.currentMemberGroup.uuid;
    },
    memberGroupName(): string {
      return this.$store.state.currentMemberGroup.uniqueName;
    },
  },
  mounted() {
    this.loadMemberGroup(this.workspaceId, this.memberGroupId);
  },
  methods: {
    loadMemberGroup(workspaceId: string, memberGroupId: string) {
      MemberGroupApi.getMemberGroup(workspaceId, memberGroupId).then((memberGroup: MemberGroup) => {
        this.members = memberGroup.members;
      });
    },
    editStudent(index: number) {
      const item = this.members[index];
      item.openEdit = true;
    },
    saveEdit(index: number) {
      const item = this.members[index];
      item.openEdit = false;
    },
    toggleConfirmed(index: number) {
      const item = this.members[index];

      const updatePaylaod = new UpdateMemberPayload(!item.confirmed);

      MemberGroupApi.updateMemberInMemberGroup(this.workspaceId, this.memberGroupId, item.uuid, updatePaylaod).then(() => {
        item.confirmed = !item.confirmed;
      });
    },
    // ---刪除個別學生資料流程---
    openRemoveModal(key: number) {
      this.$modal.show('dialog', {
        title: '確定刪除嗎?',
        text: '這個學生資料刪掉就不會再回來了',
        buttons: [
          {
            title: '取消',
            handler: () => {
              this.$modal.hide('dialog');
            },
          },
          {
            title: '確定',
            handler: () => {
              // remove operation
              this.$modal.hide('dialog');
              this.$emit('deleteStudent');
              this.deleteStudent(key);
            },
          },
        ],
      });
    },
    async deleteStudent(index: number) {
      console.log('刪除學生資料');
      if (index >= 0 && index < this.members.length) {
        MemberGroupApi.deleteMemberInMemberGroup(this.workspaceId, this.memberGroupId, this.members[index].uuid).then(() => {
          this.members.splice(index, 1);
        });
      }
    },
    // --------------
  },
});
