





























import Vue from 'vue';
import MemberGroupApi from '@/apis/MemberGroupApi';
import { MemberGroupPreview, UpdateMemberGroupPayload } from '@/apis/models/MemberGroupModel';
import { WorkspaceDataModel } from '@/apis/models/WorkspaceModel';

const MODAL_WIDTH = 656;
export default Vue.extend({
  data() {
    return {
      modalName: 'update-member-group-name-modal',
      modalWidth: MODAL_WIDTH,
      memberGroupName: '',
      remind: '',
    };
  },
  computed: {
    currentWorkspace(): WorkspaceDataModel {
      return this.$store.state.currentWorkspace;
    },
    currentMemberGroup(): MemberGroupPreview {
      return this.$store.state.currentMemberGroup;
    },
  },
  created() {
    this.modalWidth = window.innerWidth < MODAL_WIDTH ? MODAL_WIDTH / 2 : MODAL_WIDTH;
  },
  methods: {
    beforeOpen() {
      this.memberGroupName = this.currentMemberGroup.name;
      this.remind = '';
    },
    handleCancel() {
      console.log('取消創建班級');
      this.$modal.hide(this.modalName);
    },
    handleSubmit() {
      const regex = /^[^\s_!¡?÷?¿/\\+=@#$%ˆ&*(){}|~<>;:[\]]{2,}$/g;
      console.log(this.memberGroupName.search(regex));
      if (this.memberGroupName.search(regex) === 0) {
        this.remind = '';

        const updatePaylaod = new UpdateMemberGroupPayload(this.memberGroupName);

        MemberGroupApi.update(this.currentWorkspace.uuid, this.currentMemberGroup.uuid, updatePaylaod).then((newMemberGroupObject: MemberGroupPreview) => {
          this.$modal.hide(this.modalName);

          // update name and uniqueName
          this.$store.commit('changeMemberGroup', newMemberGroupObject);
        });
      } else {
        this.remind = '不允許任何空白、特殊符號，且至少要兩個字以上';
      }
    },
  },
});
